import React from 'react'
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router-dom';

const Navbar = () => {

    const location = useLocation();

    return <nav className="px-2 sm:px-4 py-5 rounded">
        <div className="container flex flex-wrap justify-between items-center mx-auto">
            <Link to="/" className={`flex z-50 transition-all items-center ${location.pathname == '/' ? 'opacity-0 cursor-default' : 'opacity-100 cursor-pointer'}`}>
                <span className="self-center text-xl font-semibold whitespace-nowrap text-light">Arya Rizky Tri Putra</span>
            </Link>
            <button data-collapse-toggle="mobile-menu" type="button" className="z-50  inline-flex items-center p-2 ml-3 text-sm  rounded-lg md:hidden focus:outline-none focus:ring-2  " aria-controls="mobile-menu" aria-expanded="false">
                <svg className="w-6 h-6" fill="#f5f6fa" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd"></path></svg>
                <svg className="hidden w-6 h-6" fill="#f5f6fa" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
            </button>
            <div className="hidden w-full md:block md:w-auto" id="mobile-menu">
                <ul className="flex flex-col mt-4 md:flex-row md:space-x-8 md:mt-0 md:text-sm md:font-medium">
                    <li className='relative group'>
                        <Link to="/skills" className={`flex text-light md:w-22 pl-14 md:pl-2 px-2 mx-auto md:h-20 group-hover:-mt-10 transition-all justify-end md:justify-start   bg-danger absolute  items-end md:pb-5 py-2 md:py-0 ${location.pathname == '/skills' ? 'md:-mt-10 md:-ml-0 -ml-10 -mt-0 md:top-0 top-10' : 'md:-mt-28 md:-ml-0 -ml-96'}`} aria-current="page">
                            SKILLS
                        </Link>
                        <Link to="/skills" className="block py-2 pr-4 pl-3 text-light rounded md:bg-transparent md:p-0 " aria-current="page">SKILLS</Link>
                    </li>
                    <li className='relative group'>
                        <Link to="/projects" className={`flex text-light md:w-22 pl-14 md:pl-2 px-2 mx-auto md:h-20 group-hover:-mt-10 transition-all justify-end md:justify-start   bg-danger absolute  items-end md:pb-5 py-2 md:py-0 ${location.pathname == '/projects' ? 'md:-mt-10 md:-ml-0 -ml-10 md:top-0 top-10' : 'md:-mt-28 md:-ml-0 -ml-96'}`} aria-current="page">
                            PROJECTS
                        </Link>
                        <Link to="/projects" className="block py-2 pr-4 pl-3 text-light rounded md:bg-transparent md:p-0 " aria-current="page">PROJECTS</Link>
                    </li>
                    <li className='relative group'>
                        <Link to="/achievements" className={`flex text-light md:w-22 pl-14 md:pl-2 px-2 mx-auto md:h-20 group-hover:-mt-10 transition-all justify-end md:justify-start   bg-danger absolute  items-end md:pb-5 py-2 md:py-0 ${location.pathname == '/achievements' ? 'md:-mt-10 md:-ml-0 -ml-10 md:top-0 top-10' : 'md:-mt-28 md:-ml-0 -ml-96'}`} aria-current="page">
                            ACHIEVEMENTS
                        </Link>
                        <Link to="/achievements" className="block py-2 pr-4 pl-3 text-light rounded md:bg-transparent md:p-0 " aria-current="page">ACHIEVEMENTS</Link>
                    </li>
                    <li className='relative group'>
                        <Link to="/experiences" className={`flex text-light md:w-22 pl-14 md:pl-2 px-2 mx-auto md:h-20 group-hover:-mt-10 transition-all justify-end md:justify-start   bg-danger absolute  items-end md:pb-5 py-2 md:py-0 ${location.pathname == '/experiences' ? 'md:-mt-10 md:-ml-0 -ml-10 md:top-0 top-10' : 'md:-mt-28 md:-ml-0 -ml-96'}`} aria-current="page">
                            EXPERIENCES
                        </Link>
                        <Link to="/experiences" className="block py-2 pr-4 pl-3 text-light rounded md:bg-transparent md:p-0 " aria-current="page">EXPERIENCES</Link>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
}

export default Navbar